.button {
  width: 80px;
  height: 80px;
  line-height: 80px;
  font-size: 32px;
  margin: 2px;
  padding: 0;
  display: inline-block;
  background-color: #f8efba;
  color: #2c3a47;
  flex: 1;
  border: 2px solid black;
}

.operator {
  background-color: #fd7272;
}

.clear {
  width: 344px;
  height: 80px;
}

