.icon-aligned {
  position: absolute;
  left: 35%;
  transform: translateY(90%);
  color: #f8efba;
  margin: 0;
  padding: 0;
  flex: 1;
}

.hero {
  transform: translateX(50px);
}

.title {
  background-color: #2c3a47;
  color: #f8efba;
  height: 80px;
  margin: 0;
}

.title-text {
  margin: 0;
  padding: 20px 0;
  font-size: 32px;
  height: 100%;
  flex: 4;
}
